.paragraph-header-home {
  background: url("../img/fons.jpg") no-repeat center center;
  background-size: cover;
  padding-bottom: clamp(20px, 3vw, 50px);
  margin-top: -80px;
  padding-top: 150px;
  text-align: center;
}

.paragraph-header-home * {
  color: #fff;
}

.paragraph-header-home .btn-cta {
  color: #54D6C6;
}

.paragraph-header-home .btn-cta:hover {
  color: #fff;
  border-color: #fff;
}

.paragraph-header-home .pre-title {
  font-weight: 600;
  font-size: 68px;
  text-transform: uppercase;
  padding-bottom: 20px;
  font-size: 61px;
}

.paragraph-header-home .pre-title span {
  display: block;
  font-weight: 300;
  font-size: 41px;
}

.paragraph-header-home h1 {
  font-size: 17px;
  font-weight: 400;
  line-height: 1.3em;
  padding-bottom: 30px;
}

.paragraph-header-home .video {
  padding: 20px 0;
  position: relative;
}

.paragraph-header-home .video:after {
  content: "";
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: url("../img/play.svg") no-repeat center center #54D6C6;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
}

.paragraph-image-text {
  padding-top: clamp(40px, 4vw, 60px);
  padding-bottom: clamp(20px, 2vw, 30px);
}

.paragraph-image-text h2 {
  padding-bottom: 30px;
}

.paragraph-image-text img {
  border-radius: 50%;
}

.paragraph-image-text.item-1 .col-image {
  position: relative;
}

.paragraph-image-text.item-1 .col-image:after {
  background: url("../img/icon-arrow-down.svg") no-repeat bottom right;
  position: absolute;
  z-index: 2;
  width: 150px;
  height: 200px;
  content: "";
  bottom: -20px;
  right: 40px;
}

.paragraph-image-text.item-2 .col-image {
  position: relative;
}

.paragraph-image-text.item-2 .col-image:after {
  background: url("../img/icon-arrow-down-v2.svg") no-repeat bottom right;
  position: absolute;
  z-index: 2;
  width: 150px;
  height: 200px;
  content: "";
  bottom: -50px;
  left: -20px;
}

.paragraph-grafic {
  margin-top: 90px;
  padding-top: clamp(20px, 4vw, 80px);
  padding-bottom: clamp(20px, 5vw, 90px);
  background-color: #EBEBEB;
}

.paragraph-grafic h2 {
  padding: 20px 0;
}

.paragraph-grafic img {
  padding-top: clamp(20px, 4vw, 60px);
}

.paragraph-grafic + .paragraph-grafic {
  margin-top: 0;
  padding-top: 20px;
}

.paragraph-actualidad-home {
  padding-top: clamp(20px, 6vw, 90px);
  padding-bottom: clamp(20px, 6vw, 100px);
  background-color: #040405;
}

.paragraph-actualidad-home h2 {
  padding-bottom: 35px;
  text-align: center;
  text-transform: uppercase;
}

.paragraph-actualidad-home h3 {
  font-size: 30px;
  padding-bottom: 20px;
  padding-top: 20px;
}

.paragraph-actualidad-home .content {
  font-weight: 300;
  font-size: 18px;
}

.paragraph-actualidad-home .content p {
  font-weight: 300;
  font-size: 18px;
}

.paragraph-actualidad-home .date {
  font-size: 12px;
  padding-top: 30px;
}

.paragraph-actualidad-home * {
  color: #fff;
}

.paragraph-highlight {
  padding-top: clamp(30px, 7vw, 100px);
  padding-bottom: clamp(30px, 7vw, 100px);
}

.paragraph-highlight * {
  color: #040405;
  text-align: center;
}

.paragraph-highlight .btn-cta {
  color: #54D6C6;
}

.paragraph-highlight .btn-cta:hover {
  color: #040405;
  border-color: #040405 !important;
}

.paragraph-highlight h2 {
  text-align: center;
}

.paragraph-highlight .content {
  padding-top: 30px;
}

.paragraph-highlight.green {
  background-color: #54D6C6;
}

.paragraph-highlight.green * {
  color: #fff;
}

.paragraph-highlight.green .btn-cta {
  border-color: #fff;
}

.paragraph-partners {
  padding-top: clamp(30px, 7vw, 100px);
  padding-bottom: clamp(30px, 8vw, 120px);
  overflow: hidden;
}

.paragraph-partners h2 {
  text-align: center;
  padding-bottom: 50px;
}

.paragraph-partners .slick-prev, .paragraph-partners .slick-next {
  background: url("../img/btn-slick.svg") no-repeat center center;
  height: 70px;
  width: 70px;
  border: 0;
  text-indent: -4000px;
  position: absolute;
  right: -90px;
  z-index: 20;
  top: 0;
  cursor: pointer;
}

.paragraph-partners .slick-prev:hover, .paragraph-partners .slick-next:hover {
  background: url("../img/btn-slick-hover.svg") no-repeat center center;
  transform: rotate(180deg);
}

.paragraph-partners .slick-track {
  display: flex;
}

.paragraph-partners .slick-dots {
  display: flex !important;
  justify-content: center;
}

.paragraph-partners .slick-dots button {
  display: none;
}

.paragraph-partners .slick-dots li:before {
  width: 15px;
  height: 15px;
}

.paragraph-partners .slick-slide {
  text-align: center;
  display: flex !important;
  height: auto;
  align-items: center;
  justify-content: center;
}

.paragraph-partners .slick-slide img {
  display: block;
  max-width: 90%;
  max-height: 70px;
}

.paragraph-partners .slick-prev {
  right: auto;
  left: -90px;
  transform: rotate(180deg);
}

.paragraph-partners .slick-prev:hover {
  transform: rotate(0deg);
  background: url("../img/btn-slick-hover.svg") no-repeat center center;
}

.paragraph-text-two-cols {
  padding-top: clamp(30px, 7vw, 100px);
  padding-bottom: clamp(30px, 4vw, 70px);
}

.paragraph-text-two-cols h2 {
  padding-bottom: 30px;
}

.paragraph-text-two-cols h3 {
  padding-bottom: 15px;
  font-size: 23px;
}

.paragraph-list-desplegable {
  padding-top: clamp(30px, 7vw, 100px);
  padding-bottom: clamp(30px, 6vw, 80px);
}

.paragraph-list-desplegable .list {
  width: 100%;
  padding-top: clamp(30px, 3vw, 50px);
}

.paragraph-list-desplegable .list ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.paragraph-list-desplegable .list ul li {
  width: 50%;
  padding: 0 40px;
}

.paragraph-list-desplegable .list ul li.hide {
  display: none;
}

.paragraph-list-desplegable .list ul li:before {
  left: 10px;
}

.paragraph-documents {
  background: url("../img/bg_corporate.jpg") no-repeat center center;
  background-size: cover;
  padding-top: clamp(30px, 7vw, 100px);
  padding-bottom: clamp(30px, 6vw, 80px);
}

.paragraph-documents h2 {
  color: #fff;
  padding-bottom: 40px;
}

.paragraph-documents ul li {
  padding-left: 0;
  margin-bottom: 35px;
}

.paragraph-documents ul li:before {
  display: none;
}

.paragraph-documents ul li a {
  color: #fff;
  text-decoration: none;
  padding-right: 50px;
  background: url("../img/download-icon.svg") no-repeat right center;
  padding-top: 5px;
  padding-bottom: 5px;
}

.paragraph-documents ul li a:hover {
  color: #54D6C6;
  background: url("../img/download-icon-hover.svg") no-repeat right center;
}
